#header h1{
    background: #33CEA5;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    padding: 10px;
    margin: 0px;
    box-shadow: 0px 0px 0px 2px #33CEA5;
    font-size: 5vmax;
    text-align: center;
    color: #FFF;
    text-transform: uppercase;
    width: 100vw;
    height: 8vmax;
    position: fixed;
    top:0px;
    z-index: 1000;
}
#wave_container{
    transform: translate(0px, 8vmax);
    padding: 0px;
    margin-bottom: 2vmax;
}

#upload{
    background-color: #33CEA5;
    border: 2px solid #33CEA5;
    outline: none;
    box-shadow: 0px 0px 3px 1px #062C30;
    font-size: 2vmax;
    color: #fff;
    padding: 5px 50px;
    border-radius: 40px;
    margin-top: 30px;
    cursor: pointer;
    transition-duration: 400ms;
}
#upload:hover{
    border: 3px solid #33CEA5;
    color: #33CEA5;
    font-weight: 400;
    background: transparent;
}
.resultbox{
    background: #150F1E;
    color: #fff;
    padding: 5px 30px;
    text-align: left;
    width: 100%;
    font-size: 20px;
}